/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.p-chain-option-card {
  position: relative;
}
.p-chain-option-card__selected-icon {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  z-index: 10;
  width: 3.888rem;
}
.p-chain-option-card.ui.card {
  width: 22.77rem;
  margin-bottom: 0.611rem;
  background-color: transparent;
}
.p-chain-option-card.ui.card.small {
  width: 15rem;
  margin: 0;
}
.p-chain-option-card.ui.card.small .p-chain-option-card__selected-icon {
  width: 2.5rem;
}
.p-chain-option-card.ui.card.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
.p-chain-option-card.ui.card.disabled:hover {
  border-color: transparent;
}
.p-chain-option-card.ui.card > .content {
  transition: background-color 0.2s ease;
  background-color: white;
}
.p-chain-option-card.ui.card > .content svg {
  width: 3.888rem;
}
.p-chain-option-card.ui.card > .content svg .cls-1 {
  transition: fill 0.2s ease;
}
.p-chain-option-card.ui.card > .content .header {
  text-align: left;
}
.p-chain-option-card.ui.card > .content .header,
.p-chain-option-card.ui.card > .content .description {
  transition: color 0.2s ease;
}
.p-chain-option-card.ui.card > .content p {
  text-align: left;
  width: 100%;
  margin-right: 1rem;
  margin-bottom: 0;
}
.p-chain-option-card.ui.card > .content .description {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.p-chain-option-card.ui.card:hover:not(.disabled) .content {
  background-color: #046961;
}
.p-chain-option-card.ui.card:hover:not(.disabled) .content .header,
.p-chain-option-card.ui.card:hover:not(.disabled) .content .description {
  color: white;
}
.p-chain-option-card.ui.card:hover:not(.disabled) .content svg *[class*="cls-1"] {
  fill: #033030;
}
