/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.progress-bars {
  background: #033030;
  width: 100%;
  height: 7.222rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}
.progress-bars__bar {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 1.666rem 0 0;
  position: relative;
}
.progress-bars__bar:first-child {
  margin-left: 1.666rem;
}
.progress-bars__bar .ui.header {
  font-weight: normal;
  color: white;
}
.progress-bars__bar .ui.progress > .label {
  margin-top: 0;
  top: 0;
  height: 100%;
}
.progress-bars__bar .ui.progress > .label button {
  position: absolute;
  top: -110%;
  transform: translate(-50%, -50%);
  right: 0;
  left: 100%;
  margin-bottom: 1rem;
}
.progress-bars__bar .ui.progress:last-child {
  margin-bottom: 0;
}
.progress-bars__bar__limit {
  position: absolute;
  border-right: 2px dashed #B3B2B3;
  height: 100%;
  bottom: 0;
}
