@import '../../semantic-ui/site/globals/site.variables';

.p-chain-option-card {
  position: relative;
  
  &__selected-icon {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
    z-index: 10;
    width: 3.888rem;
  }

  &.ui.card {
    width: 22.77rem;
    margin-bottom: 0.611rem;
    background-color: transparent;

    &.small {
      width: 15rem;
      margin: 0;
      
      .p-chain-option-card__selected-icon {
        width: 2.5rem;
      }
    }

    &.disabled {
      opacity: 0.3;
      cursor: not-allowed;
      &:hover {
        border-color: transparent;
      }
    }

    > .content {
      transition: background-color .2s ease;
      background-color: white;

      svg {
        width: 3.888rem;
        .cls-1 {
          transition: fill .2s ease;
        } 
      }

      .header {
        text-align: left;
      }
      .header,
      .description {
        transition: color .2s ease;
      }

      p {
        text-align: left;
        width: 100%;
        margin-right: 1rem;
        margin-bottom: 0;
      }

      .description {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
      }
    }
  }
  
  &.ui.card:hover:not(.disabled) {

    .content {
      background-color: @green;
      
      .header,
      .description {
        color: white;
      }

      svg *[class*="cls-1"]  {
        fill: @darkGreen;
      }
    }
  }
}