/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
@media only screen and (min-width: 768px) {
  .chat {
    position: fixed;
    top: 0;
    right: 0;
    padding-right: 0;
    height: 100vh;
    z-index: 1;
    width: 25%;
  }
}
.chat__navbar {
  height: 3.333rem;
  background-color: #046961;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chat__navbar p {
  margin: 0 0 0 3.6111rem;
  line-height: 1;
}
.chat__navbar__icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 25%;
  margin-right: 1rem;
}
.chat__navbar__icons svg {
  width: 1.5rem;
  height: 1.5rem;
}
.chat__navbar__icons svg .cls-1 {
  fill: white;
}
.chat .segment {
  height: 100%;
}
.chat__wrapper.ui.segment {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 0;
  border-right: 0;
}
@media only screen and (min-width: 768px) {
  .chat__wrapper.ui.segment > div {
    margin-top: 3.2rem;
  }
}
.chat__messages {
  padding: 0.555rem;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 13rem);
  overflow-y: scroll;
  background-color: #DBDEDF;
}
.chat__messages__message {
  margin-right: 4em;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1.3888rem;
  margin-left: 0.5em;
}
.chat__messages__message p {
  text-align: left;
}
.chat__messages__message p:first-child {
  color: #046961;
  margin-bottom: 0;
}
.chat__messages__message__user-icon {
  border-radius: 100%;
  background: #B3B2B3;
  height: 3.333rem;
  width: 3.333rem;
  min-width: 3.333rem;
  min-height: 3.333rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.555rem;
  position: relative;
}
.chat__messages__message__user-icon img {
  border-radius: 100%;
}
.chat__messages__message__user-icon p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0;
  border: none;
  font-size: 0.9em;
  line-height: 0.9em;
}
.chat__messages__message__bubble {
  background-color: #F5F5F5;
  border-radius: 20px;
  border: 1px solid lightgray;
  padding: 0.8em 1em;
  max-width: 60%;
}
.chat__messages__message--user {
  flex-direction: row-reverse;
  margin-right: 0;
}
.chat__messages__message--user .chat__messages__message__user-icon {
  margin-left: 0.555rem;
}
.chat__messages__message--user .chat__messages__message__bubble {
  background-color: #046961;
  color: white;
}
.chat form.ui.form {
  padding: 1em 1em;
  display: flex;
  align-items: flex-end;
  border-top: 1px solid lightgray;
}
@media only screen and (min-width: 768px) {
  .chat form.ui.form {
    position: fixed;
    bottom: 0;
    background: white;
    width: 25%;
  }
}
.chat form.ui.form textarea {
  margin-right: 1em;
  resize: none;
  background-color: #DBDEDF;
  border-radius: 20px;
  height: 4.4rem;
}
.chat form.ui.form button.pulsate {
  position: relative;
}
.chat form.ui.form button.pulsate:before {
  content: '';
  position: absolute;
  display: block;
  width: 300%;
  height: 300%;
  box-sizing: border-box;
  top: -100%;
  left: -100%;
  border-radius: 100%;
  background-color: #16C774;
  animation: pulse-ring 1.5s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}
@keyframes pulse-ring {
  0% {
    transform: scale(0.33);
  }
  80%,
  100% {
    opacity: 0;
  }
}
@keyframes pulse-dot {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}
