/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.p-chain-step .ui.header {
  color: #046961;
  margin-bottom: 0;
}
.p-chain-step .ui.header.medium {
  margin: 0 0 0;
  font-size: 1.5rem;
}
.p-chain-step__6 .p-chain-column {
  align-items: center;
  display: flex;
  flex-direction: column;
}
.p-chain-step__columns {
  display: flex;
  justify-content: center;
  height: 100%;
  padding-top: 0;
}
.p-chain-step__columns__image {
  margin: 0.5rem 0;
  width: 45%;
  padding: 0 2rem;
}
.p-chain-step__columns__image img {
  width: 100%;
}
