/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.landing {
  width: 100%;
  position: relative;
  height: calc(100vh - 17rem);
}
.landing__content {
  padding: 1rem 0 0.5rem 0;
  text-align: center;
  height: 100%;
  overflow-y: auto;
}
.landing__content__footer {
  height: 6.666rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.landing__content__footer img {
  width: 165px;
  margin-left: 2.5rem;
}
.landing__content__footer button.ui {
  margin-right: 1.7rem;
}
@media only screen and (min-width: 768px) {
  .landing-frontpage-img {
    margin-top: 1em;
  }
}
