/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.p-chain-column {
  padding: 0.5rem 0;
  width: 100%;
}
.p-chain-column.striped:nth-child(even) {
  background-color: #EEEEEE;
  padding: 1 0.5rem;
}
.p-chain-column > .ui.medium.header {
  color: black;
  font-size: 1.5rem;
}
.p-chain-column.wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.p-chain-column.wrap .ui.card {
  margin-top: 0;
}
.p-chain-column.wrap .ui.card:last-child {
  margin-bottom: 0.611rem;
}
