/* colors (from LIFE Designguide) */
/* fonts */
.chat-ellipsis-loader {
  display: flex;
  position: relative;
  min-width: 2.777rem;
  min-height: 0.555rem;
}
.chat-ellipsis-loader__circle {
  width: 0.555rem;
  height: 0.555rem;
  background: #046961;
  border-radius: 100%;
  margin: 0 0.166rem;
  animation: jump 1.5s linear infinite;
  position: absolute;
}
@keyframes jump {
  0% {
    margin-top: 0;
  }
  15% {
    margin-top: -0.555rem;
  }
  30% {
    margin-top: 0px;
  }
}
.chat-ellipsis-loader #b {
  animation-delay: 0.2s;
  left: 0.833rem;
}
.chat-ellipsis-loader #c {
  animation-delay: 0.4s;
  left: 1.666rem;
}
