/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.feedback-modal__feedback {
  margin-bottom: 2rem;
}
.feedback-modal__feedback .ui.header {
  display: flex;
  align-items: center;
}
.feedback-modal__feedback__positive,
.feedback-modal__feedback__negative {
  padding: 0 0.7rem;
  color: #FD535A;
}
.feedback-modal__feedback__positive {
  color: #16C774;
}
.feedback-modal__feedback:last-child {
  margin-bottom: 0;
}
.feedback-modal__feedback .ui.header {
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.feedback-modal__feedback .ui.header:first-child {
  margin-top: 0;
}
