/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.to-director-modal.ui.modal {
  border-radius: 15px;
}
.to-director-modal.ui.modal .header,
.to-director-modal.ui.modal:first-child {
  border-radius: 15px;
}
.to-director-modal.ui.modal .actions {
  border-top: 0;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}
.to-director-modal.ui.modal > .content {
  padding: 0 !important;
  border-radius: 15px !important;
}
.to-director-modal.ui.modal > .content .description {
  padding: 1rem 2.222rem;
}
.to-director-modal__fields {
  margin-bottom: 1rem;
}
.to-director-modal__fields__input {
  display: flex;
  border-bottom: 2px solid #EEEEEE;
  color: #ADADAD;
  align-items: center;
}
.to-director-modal__fields__input p {
  margin: 0;
  padding: 0;
}
.to-director-modal__fields__input p:first-child {
  width: 3.888rem;
  padding: 0.5rem 1.9444rem 0.5rem 1.3888rem;
}
.to-director-modal__fields__input p:nth-child(2) {
  margin-left: 1.9444rem;
}
.to-director-modal__fields__input__subject {
  color: black;
}
.to-director-modal .ui.form textarea {
  border: 0;
  margin-left: 0.5rem;
}
.to-director-modal__email_navbar {
  width: 100%;
  height: auto;
}
.to-director-modal__email-footer-icons {
  width: 80%;
  float: left;
  margin-left: 10px;
}
.to-director-modal__answer > p {
  padding: 3rem 0 2rem 2rem;
  font-size: 1.5rem;
}
.to-director-modal__answer__from-card {
  display: flex;
  align-items: center;
}
.to-director-modal__answer__from-card img {
  width: 7rem;
  border-radius: 100%;
  margin-right: 2rem;
}
.to-director-modal__answer__from-card__content__name span {
  font-size: 1.6rem;
}
.to-director-modal__answer__from-card__content__to {
  display: flex;
  align-items: center;
}
.to-director-modal__answer__from-card__content__to p {
  margin: 0;
  font-size: 1.2rem;
}
.to-director-modal__answer__from-card__content__to svg {
  width: 1.2rem;
  margin: 0 0.5rem;
  margin-bottom: 0.2rem;
}
.to-director-modal__answer__logo {
  width: 40%;
  margin-left: 2rem;
  display: flex;
  align-items: flex-end;
  border-top: 1px solid #EEEEEE;
  padding: 1rem 0;
}
.to-director-modal__answer__logo p {
  font-size: 1.2rem;
  margin: 0;
}
.to-director-modal__answer__logo img {
  width: 3.888rem;
  margin: 0 1rem 0 0;
}
